import React from "react";

function Table({ data, columnsOnMainTable, columnProperties, uid }) {
  // Helpers
  const renderTableHead = (colNames) => {
    return (
      <thead>
        <tr>
          {colNames.map((colName) => (
            <th key={colName}>{columnProperties[colName].display}</th>
          ))}
        </tr>
      </thead>
    );
  };

  const renderTableBody = (data, colNames) => {
    return data.map((datum, i) => renderTableRow(datum, colNames, i));
  };

  const renderTableRow = (datum, colNames, i) => {
    return (
      <tr
        key={datum[uid]}
        style={
          i % 2 === 0
            ? { backgroundColor: "#f5f5f5" }
            : { backgroundColor: "#fafafa" }
        }
      >
        {colNames.map((colName) => (
          <td key={datum[colName]}>
            <code>{datum[colName]}</code>
          </td>
        ))}
      </tr>
    );
  };

  // Main
  return (
    <div>
      <table>
        {renderTableHead(columnsOnMainTable)}
        <tbody>{renderTableBody(data, columnsOnMainTable)}</tbody>
      </table>
    </div>
  );
}

export default Table;
